
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Search from 'vux/src/components/search/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import specialDye2 from '../page/apply/specialDye/specialDye2.vue'
export default {
  components: {
    Actionsheet,specialDye2,
    XSwitch,
    Toast,
    Search
  },
  data () {
    return {
      value: 'test',
      results:[],
    }
  },
  methods: {
    resultClick(item){
      alert( JSON.stringify(item) )
    },
    getResult (val) {
      this.results = val ? getResult(this.value) : []
    }
  }
}

function getResult (val) {
  let rs = []
  for (let i = 0; i < 20; i++) {
    rs.push({
      title: `${val} result: ${i + 1} `,
      other: i
    })
  }
  return rs
}



